/* Global page settings */
* {
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: lighter;
  box-sizing: border-box;
}

.Social-media {
  margin: 50px 0 50px 0;
}

.card{
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
}


/* Width setting for the contact page container */
.container {

    width: 80%;
    margin-left: 10%;
    border-radius: 5px;
    margin-bottom: 200px;
    padding: 2%;
 
   
}

.contact-form {
    box-shadow: 0 0 5px black;
    padding: 20px;
    /* background-color: rgba(245, 249, 254, 0.981); */
}

/* Styling for the input boxes */
input {
    width: 100%;
    padding: 5px;
    border-radius: 5px;

}

/* Styling for the textarea box */
textarea {
    width: 100%;
    height: 10vh;
    padding: 5px;
    border-radius: 5px;

}

/* Margin for error message div  */
.err {
    margin: 3vh 0 3vh 0;
}

/* Margin for h5 element  */
h6 {
    margin: 3vh 0 0 0;
}

/* Styling for submit button */
button {
    margin-top: 3vh;
    background-color: rgb(75, 69, 69);
    color: white;
    padding: 5px;
    border-radius: 10px;
}

.contact-form {

    text-align: center;
}

.form-text {
    text-align: left;
    margin: 3vh 0 1vh 0;
}
/* Styling for anout me page container */
.container {
    width: 50%;
    margin-left: 25%;
    margin-top: 10vh;
    margin-bottom: 200px;

}

/* Size and position of profile picture */
.profilePic {
    width: 30%;
    margin-left: 35%;
    margin-bottom: 10vh;
}


/* Margin and font weight for h3 elements  */
h3 {
    margin: 3vh 0 3vh 0;
    font-weight: bold;
}


/* Styling for p elements on about me page */
p {
    font-size: smaller;
    line-height: 1.5;
    margin-top: 3vh;
    text-align: justify;
}

/* Padding and margin for bio div */
.bio {
    padding: 0 15px 15px 15px;
    margin-bottom: 10vh;
}

/* Media settings for small screen responsiveness */
@media only screen and (max-width: 768px) {
    .card {
       right: 15%;
        display: block;
        width: 90%;
        height: 450px;
        padding-bottom: 5px;
    }

    .container {
        width: 90%;
        margin-left: 5%;
        min-height: -webkit-fit-content;
        min-height: -moz-fit-content;
        min-height: fit-content;
    }

    .profilePic {
        width: 50%;
        margin-left: 25%;
    }
}
